<script>
export default {
  name: "Request",
  data() {
    return {
      step: 1,
      contact: {
        name: "",
        phone: "",
        email: ""
      },
      project: {
        name: "",
        description: "",
        user: "",
        platform: "",
        budget: ""
      },
      error: ""
    };
  },
  methods: {
    nextStep(event) {
      event.preventDefault();
      this.step += 1;
      document.body.scrollTop = 0; // For Safari
      document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
    },
    prevStep(event) {
      event.preventDefault();
      this.step -= 1;
      document.body.scrollTop = 0; // For Safari
      document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
    },
    submit(event) {
      event.preventDefault();
      var submit_button = document.getElementById("submit-button");
      submit_button.innerHTML =
        '<span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>Submitting...';
      this.axios
        .post(process.env.VUE_APP_API_URL + "/api/v1/request_projects", {
          name: this.contact.name,
          phone: this.contact.phone,
          email: this.contact.email,
          project_name: this.project.name,
          description: this.project.description,
          user_project: this.project.user,
          platform: this.project.platform,
          budget_estimation: this.project.budget.split(",").join(""),
          isRead: 0
        })
        .then(response => {
          if (response.data == "Record has been created") {
            submit_button.innerHTML = "SUBMIT";
            this.step += 1;
          } else {
            submit_button.innerHTML = "SUBMIT";
            this.showError(response.data);
          }
        })
        .catch(error => {
          let modalMessage = "";
          for (let message in error.response.data.errors) {
            modalMessage += error.response.data.errors[message][0] + "<br>";
          }
          submit_button.innerHTML = "SUBMIT";
          this.showError(modalMessage);
        });
    },
    showModal() {
      this.$bvModal.show("modal-clear-form");
    },
    closeModal() {
      this.$bvModal.hide("modal-clear-form");
    },
    showError(message) {
      this.error = message;
      this.$bvModal.show("modal-failed");
    },
    closeError() {
      this.$bvModal.hide("modal-failed");
    },
    formatter(value) {
      let res = "";
      let temp = value.split(",").join("");
      for (let i = 0; i < temp.length; i++) {
        if ((temp.length - i) % 3 == 0 && i != 0) {
          res += ",";
        }
        res += temp[i];
      }
      return res;
    },
    isNumber(event) {
      event = event ? event : window.event;
      var charCode = event.which ? event.which : event.keyCode;
      if (
        charCode > 31 &&
        (charCode < 48 || charCode > 57) &&
        charCode !== 46
      ) {
        event.preventDefault();
      } else {
        return true;
      }
    },
    clearForm(event) {
      event.preventDefault();
      this.contact = {
        name: "",
        phone: "",
        email: ""
      };
      this.project = {
        name: "",
        description: "",
        user: "",
        platform: "",
        budget: ""
      };
      this.closeModal();
    }
  }
};
</script>

